
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
body {
  margin: 35px 100px;
}
h3 {
  font-weight: normal;
  margin: 0;
  font-size: inherit;
}
th,
td {
  border: 1px solid;
  border-top: 0;
  border-right: 0;
}

.table-section {
  text-align: right;
  max-width: 450px;
  width: 100%;
  position: relative;
}
.table {
  min-width: 330px;
  border-collapse: collapse;
  border: 1px solid;
  border-bottom: 0;
  border-left: 0;
}
.table__header,
.table__body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.table__body {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.table__header-row {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.table__header-item {
  padding: 10px 0;
  font-weight: normal;
  text-align: left;
}
.table__header-item h3 {
  padding-left: 30px;
}
.table__header-item:first-child {
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
}
.table__header-item:nth-child(2) {
  -ms-flex-preferred-size: 60%;
      flex-basis: 60%;
}
.btn {
  border-radius: 8px;
  border: 1px solid;
  background-color: rgba(0, 0, 0, 0.15);
  outline: none;
  cursor: pointer;
}

td {
  -webkit-box-shadow: 0 -1px 0 0 black;
          box-shadow: 0 -1px 0 0 black;
}
/* .table__body > .table__body-container {
  box-shadow: 0 -1px 0 0 black;
} */
.table__body-row--main {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.table__body-item {
  text-align: center;
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.table__body-item--name {
  position: relative;
  padding: 20px 20px 20px 22px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.table__body-item--phone {
  padding: 20px 25px;
  -ms-flex-preferred-size: 60%;
      flex-basis: 60%;
}
.table__body-row--subordinates {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.table__body-row--subordinates > .table__body-container {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.has-children {
  cursor: pointer;
}
.has-children::before {
  content: '+';
  position: absolute;
  top: 35%;
  left: 4%;
  width: 20px;
  height: 20px;
}

.addModal {
  position: absolute;
  right: -175%;
  top: 64px;
  width: 95%;
  height: 360px;
  border: 1px solid;
  padding: 15px 25px;
  text-align: left;
}
.addModal__heading {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;

  margin-bottom: 40px;
}
.addModal__heading--close {
  cursor: pointer;
}
.addModal__form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.addModal__form-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
}
.addModal__form-item + .addModal__form-item {
  margin-top: 30px;
}
.addModal__form-item input,
.addModal__form-item select {
  padding: 8px;
  outline: none;
  width: 240px;
}
.addModal__form-item select {
  border-radius: 6px;
}
.save-btn {
  text-align: left;
  padding: 9px 85px 9px 21px;

  margin-top: 30px;
}

.add-btn {
  padding: 6px 27px;
  margin-bottom: 35px;
}
